<script>
import Vue from 'vue'
import Swiper from "swiper";
import axios from 'axios'

export default {
    data() {
		return {
            activeIndex: '0',
			roleMenus: [{"backMenu":[{"child":[{"allButtons":["","","","",""],"appFrontIcon":"cuIcon-attentionfavor","buttons":["","","",""],"menu":"","menuJump":"","tableName":"xuesheng"}],"menu":""},{"child":[{"allButtons":["","","",""],"appFrontIcon":"cuIcon-goodsnew","buttons":["","","",""],"menu":"","menuJump":"","tableName":"jiaoshi"}],"menu":""},{"child":[{"allButtons":["","","","","",""],"appFrontIcon":"cuIcon-link","buttons":["","","","",""],"menu":"","menuJump":"","tableName":"kechengxinxi"}],"menu":""},{"child":[{"allButtons":["","","",""],"appFrontIcon":"cuIcon-camera","buttons":["","","",""],"menu":"","menuJump":"","tableName":"kechengfenlei"}],"menu":""},{"child":[{"allButtons":["","","","","","",""],"appFrontIcon":"cuIcon-medal","buttons":["","","","",""],"menu":"","menuJump":"","tableName":"xuanzekecheng"}],"menu":""},{"child":[{"allButtons":["","","",""],"appFrontIcon":"cuIcon-album","buttons":["","","",""],"menu":"","menuJump":"","tableName":"banji"}],"menu":""},{"child":[{"allButtons":["","","",""],"appFrontIcon":"cuIcon-circle","buttons":["","",""],"menu":"","menuJump":"","tableName":"chengjitongzhi"}],"menu":""},{"child":[{"allButtons":["","","",""],"appFrontIcon":"cuIcon-shop","buttons":["","","",""],"menu":"","menuJump":"","tableName":"jiaoshixinxi"}],"menu":""},{"child":[{"allButtons":["","","",""],"appFrontIcon":"cuIcon-qrcode","buttons":["","","",""],"menu":"","tableName":"config"},{"allButtons":["","","",""],"appFrontIcon":"cuIcon-news","buttons":["","","",""],"menu":"","tableName":"news"},{"allButtons":["","","",""],"appFrontIcon":"cuIcon-news","buttons":["","","",""],"menu":"","tableName":"newstype"},{"allButtons":["",""],"appFrontIcon":"cuIcon-list","buttons":["",""],"menu":"","tableName":"aboutus"},{"allButtons":["",""],"appFrontIcon":"cuIcon-addressbook","buttons":["",""],"menu":"","tableName":"systemintro"}],"menu":""}],"frontMenu":[{"child":[{"allButtons":["","","","","",""],"appFrontIcon":"cuIcon-discover","buttons":["",""],"menu":"","menuJump":"","tableName":"kechengxinxi"}],"menu":""},{"child":[{"allButtons":["","","",""],"appFrontIcon":"cuIcon-medal","buttons":[""],"menu":"","menuJump":"","tableName":"jiaoshixinxi"}],"menu":""}],"hasBackLogin":"","hasBackRegister":"","hasFrontLogin":"","hasFrontRegister":"","roleName":"","tableName":"users"},{"backMenu":[{"child":[{"allButtons":["","","","","","",""],"appFrontIcon":"cuIcon-medal","buttons":[""],"menu":"","menuJump":"","tableName":"xuanzekecheng"}],"menu":""},{"child":[{"allButtons":["","","",""],"appFrontIcon":"cuIcon-circle","buttons":[""],"menu":"","menuJump":"","tableName":"chengjitongzhi"}],"menu":""}],"frontMenu":[{"child":[{"allButtons":["","","","","",""],"appFrontIcon":"cuIcon-discover","buttons":["",""],"menu":"","menuJump":"","tableName":"kechengxinxi"}],"menu":""},{"child":[{"allButtons":["","","",""],"appFrontIcon":"cuIcon-medal","buttons":[""],"menu":"","menuJump":"","tableName":"jiaoshixinxi"}],"menu":""}],"hasBackLogin":"","hasBackRegister":"","hasFrontLogin":"","hasFrontRegister":"","roleName":"","tableName":"xuesheng"},{"backMenu":[{"child":[{"allButtons":["","","","","",""],"appFrontIcon":"cuIcon-link","buttons":["","","","",""],"menu":"","menuJump":"","tableName":"kechengxinxi"}],"menu":""},{"child":[{"allButtons":["","","","","","",""],"appFrontIcon":"cuIcon-medal","buttons":["","","",""],"menu":"","menuJump":"","tableName":"xuanzekecheng"}],"menu":""},{"child":[{"allButtons":["","","",""],"appFrontIcon":"cuIcon-shop","buttons":[""],"menu":"","menuJump":"","tableName":"jiaoshixinxi"}],"menu":""}],"frontMenu":[{"child":[{"allButtons":["","","","","",""],"appFrontIcon":"cuIcon-discover","buttons":["",""],"menu":"","menuJump":"","tableName":"kechengxinxi"}],"menu":""},{"child":[{"allButtons":["","","",""],"appFrontIcon":"cuIcon-medal","buttons":[""],"menu":"","menuJump":"","tableName":"jiaoshixinxi"}],"menu":""}],"hasBackLogin":"","hasBackRegister":"","hasFrontLogin":"","hasFrontRegister":"","roleName":"","tableName":"jiaoshi"}],
			baseUrl: '',
			carouselList: [],
			menuList: [],
			form: {
				ask: '',
				userid: localStorage.getItem('frontUserid')
			},
			headportrait: localStorage.getItem('frontHeadportrait')?localStorage.getItem('frontHeadportrait'):'',
			Token: localStorage.getItem('frontToken'),
            username: localStorage.getItem('username'),
            notAdmin: localStorage.getItem('frontSessionTable')!='"users"',
			timer: '',
			iconArr: [
				'el-icon-star-off',
				'el-icon-goods',
				'el-icon-warning',
				'el-icon-question',
				'el-icon-info',
				'el-icon-help',
				'el-icon-picture-outline-round',
				'el-icon-camera-solid',
				'el-icon-video-camera-solid',
				'el-icon-video-camera',
				'el-icon-bell',
				'el-icon-s-cooperation',
				'el-icon-s-order',
				'el-icon-s-platform',
				'el-icon-s-operation',
				'el-icon-s-promotion',
				'el-icon-s-release',
				'el-icon-s-ticket',
				'el-icon-s-management',
				'el-icon-s-open',
				'el-icon-s-shop',
				'el-icon-s-marketing',
				'el-icon-s-flag',
				'el-icon-s-comment',
				'el-icon-s-finance',
				'el-icon-s-claim',
				'el-icon-s-opportunity',
				'el-icon-s-data',
				'el-icon-s-check'
			],
			bottomContent: '',
		}
    },
    created() {
		this.baseUrl = this.$config.baseUrl;
		this.menuList = this.$config.indexNav;
		this.getCarousel();
        if(localStorage.getItem('frontToken') && localStorage.getItem('frontToken')!=null) {
			this.getSession()
        }
    },
    mounted() {
        this.activeIndex = localStorage.getItem('keyPath') || '0';


		// banner
		setTimeout(()=>{
			new Swiper(".mySwiper3", {"navigation":{"nextEl":".swiper-button-next","prevEl":".swiper-button-prev"},"pagination":{"el":".swiper-pagination","clickable":true},"autoplay":{"delay":2500,"disableOnInteraction":false},"effect":"fade"})
		}, 500)

    },
    computed: {
		activeMenu() {
			const route = this.$route
			const {
				meta,
				path
			} = route
			// if st path, the sidebar will highlight the path you sete
			if (meta.activeMenu) {
				return meta.activeMenu
			}
			return path
		},
    },
    watch: {
        $route(newValue) {
            let that = this
            let url = window.location.href
            let arr = url.split('#')
            for (let x in this.menuList) {
                if (newValue.path == this.menuList[x].url) {
                    this.activeIndex = x
                }
            }
            this.Token = localStorage.getItem('frontToken')
            if(arr[1]!='/index/home'){
            	var element = document.getElementById('scrollView');
            	var distance = element.offsetTop;
            	window.scrollTo( 0, distance )
            }else{
            	window.scrollTo( 0, 0 )
            }
        },
		headportrait(){
			this.$forceUpdate()
		},
    },
    methods: {

		async getSession() {
			await this.$http.get(`${localStorage.getItem('UserTableName')}/session`, {emulateJSON: true}).then(async res => {
				if (res.data.code == 0) {
					localStorage.setItem('sessionForm',JSON.stringify(res.data.data))
					localStorage.setItem('frontUserid', res.data.data.id);
					if(res.data.data.vip) {
						localStorage.setItem('vip', res.data.data.vip);
					}
					if(res.data.data.touxiang) {
						this.headportrait = res.data.data.touxiang
						localStorage.setItem('frontHeadportrait', res.data.data.touxiang);
					} else if(res.data.data.headportrait) {
						this.headportrait = res.data.data.headportrait
						localStorage.setItem('frontHeadportrait', res.data.data.headportrait);
					}
				}
			});
		},
        handleSelect(keyPath) {
            if (keyPath) {
                localStorage.setItem('keyPath', keyPath)
            }
        },
		toLogin() {
		  this.$router.push('/login');
		},
        logout() {
            localStorage.clear();
            Vue.http.headers.common['Token'] = "";
            this.$router.push('/index/home');
            this.activeIndex = '0'
            localStorage.setItem('keyPath', this.activeIndex)
            this.Token = ''
            this.$forceUpdate()
            this.$message({
                message: '',
                type: 'success',
                duration: 1000,
            });
        },
		getCarousel() {
			this.$http.get('config/list', {params: { page: 1, limit: 3 }}).then(res => {
				if (res.data.code == 0) {
					this.carouselList = res.data.data.list;
				}
			});
		},
		// 
		carouselClick(url) {
			if (url) {
				if (url.indexOf('https') != -1) {
					window.open(url)
				} else {
					this.$router.push(url)
				}
			}
		},
		goBackend() {
			localStorage.setItem('Token', localStorage.getItem('frontToken'));
			localStorage.setItem('role', localStorage.getItem('frontRole'));
			localStorage.setItem('sessionTable', localStorage.getItem('frontSessionTable'));
			localStorage.setItem('headportrait', localStorage.getItem('frontHeadportrait'));
			localStorage.setItem('userid', localStorage.getItem('frontUserid'));
			window.open(`${this.$config.baseUrl}admin/dist/index.html`, "_blank");
		},
		goMenu(path) {
            this.$router.push(path);
		},
    }
}
</script>

